@import url(https://fonts.googleapis.com/css2?family=Lora&family=Montserrat&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* @import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Roboto&display=swap'); */
 
:root {
  --theme-col-1: hsl(199, 78%, 42%);
  --theme-col-2: hsl(0,0%,0%);
  font-size: 16px;
}
/* color scheme */
:root {
  --cyan:hsl(187, 63%, 57%);
  --red: hsl(358, 68%, 57%);
  --dark-blue:hsl(209, 40%, 40%);
  --dark-green:hsl(115, 40%, 40%);
  --orange:hsl(35, 90%, 60%);
}

.App {
  display:grid;
  grid-template-areas: 
  'header header header'
  'main main main'
  'footer footer footer';
  place-content: center;
  place-items: center;
  font-family: 'Arial', sans-serif;
  text-align: justify;
}

.title, h1, h2, h3, header {
  font-family: 'Lora', serif;
  text-align: center;
}

.App > header {
  z-index: 10;
  grid-area: header;
  display:grid;
  grid-auto-flow: column;
  grid-template-columns: 1fr 1fr;
  font-size: 1.1rem;
  grid-template-areas: 
  "logo logo"
  "navToggle socials";
  place-items: center;
  place-content: center;
  grid-gap:2vh 20vw;
  gap:2vh 20vw;
  width:100%;
  padding-bottom: 1vh;
  background-color: white;
  transition: all 0.3s;
}

.App > header > .socials {
  grid-area: socials;
  justify-self: flex-end;
}

header > nav {
  display:grid;
  padding:2vh 2vw;
  grid-auto-flow: row;
  grid-gap:2ch;
  gap:2ch;
  place-content: center;
  place-items: center;
  align-items: flex-end;
  position: fixed;
  top:0;
  grid-area: nav;
  background-color: hsl(115, 40%, 40%);
  background-color: var(--dark-green);
  color:white;
  transform: translateX(-100%);
  transition: transform 0.3s ease-out;
  height:100vh;
  box-shadow: 1px 0 2px #222;
  font-size: 1rem;
}

header > nav > a {
  display: grid;
  place-content: center;
  width:100%;
  height:5vh;
  text-align: center;
  /* border: 2px solid black;
  border-radius: 5px; */
}

#logo {
  grid-area: logo;
  height: 15vh;
}


#nav-toggler {
  grid-area: navToggle;
  display: flex;
  place-content: center;
  place-items: center;
  flex-direction: column;
  font-size: 10vh;
}
#nav-toggler div {
  height:3px;
  background-color: black;
  width:25px;
  margin:3px;
  transition: transform 0.3s;
}

#nav-toggler.toggled div:first-child {
  transform: translateX(0px) translateY(9px) rotate(-45deg)
}
#nav-toggler.toggled div:nth-child(2) {
  transform: scale(0)
}
#nav-toggler.toggled div:nth-child(3) {
  transform: translateX(0px) translateY(-9px) rotate(45deg)
}

header > nav.toggled {
  transform: translateX(0);
}

header > nav > a {
  color: inherit;
  text-decoration: none;
  transition: color 0.3s;
}

header a:hover {
  color:hsl(35, 90%, 60%);
  color:var(--orange);
}
header > nav > a.nav-active {
  color:black;
}

footer > nav {
  display: none;
}

.App > main {
  grid-area: main;
  min-width: 80vw;
  min-height: 80vh;
  width:100vw;
  margin-bottom: 10vh;
}

.App > footer {
  grid-area: footer;
  height:7vh;
  display: grid;
  grid-auto-flow: column;
  place-items: center;
  color:white;
  background-color: #222;
  width:100%;
  font-size: 0.8rem;
}

.App > footer a {
  color:inherit;
}

.socials {
  display:grid;
  place-content: space-around;
  grid-auto-flow: column;
  width:100%;
  color:inherit;
}
.socials > a {
  color:inherit;
}

#loader {
  position: fixed;
  top:calc(50vh - 2.5vmin);
  left:calc(50vw - 7.5vmin);
  display: none;
  grid-auto-flow: column;
  grid-gap:1vmin;
  gap:1vmin;
}
#loader.visible {
  display: grid;
}
#loader div {
  border-radius: 100%;
  width:5vmin;
  height:5vmin;
  background-color:hsl(199, 78%, 42%);
  background-color:var(--theme-col-1);
  animation: rescale 1.3s linear infinite;
  transform: scale(0.5);
}

#loader div:nth-child(1) {
  animation-delay: 0s;
}
#loader div:nth-child(2) {
  animation-delay: 0.3s;
}
#loader div:nth-child(3) {
  animation-delay: 0.6s;
}

.language > button {
  border:none;
  background:none;
  color:inherit;
  text-decoration: underline;
  cursor: pointer;
  font-size: inherit;
}

@media screen and (min-width: 768px){
  .App > header {
    grid-template-areas: "logo nav socials";
    position: -webkit-sticky;
    position: sticky;
    grid-gap:2ch;
    gap:2ch;
    top:0px;
    /* place-items: space-between;
    place-content: space-between; */
    align-items: unset;
    grid-template-columns: unset;
    width:98.9vw;
  }
  .App > header.isSticky {
    grid-template-areas: "nav socials";
    /* background-color: #222; */
    /* color:white; */
    padding-top: 1vh;
    box-shadow: 0 2px 1px black;
    place-content: center;
    place-items: center;
  }
  .App > header.isSticky > #logo {
    display: none;
  }

  .App > header > #logo {
    padding-right: 5vw;
  }

  .App > header .nav-active {
    color:hsl(35, 90%, 60%);
    color:var(--orange);
  }
  .App > header.isSticky .nav-active {
    color:hsl(35, 90%, 60%);
    color:var(--orange);
  }
  header > nav {
    display:grid;
    grid-auto-flow: column;
    color:inherit;
    background: none;
    position: unset;
    height:auto;
    box-shadow: none;
    padding: 0;
    grid-gap:2vw;
    gap:2vw;
    transform: translateX(0);
    font-size: inherit;
  }

  footer > nav {
    display: grid;
    grid-auto-flow: column;
    grid-gap:1vw;
    gap:1vw;
  }

  #nav-toggler {
    display:none;
    position: relative;
  }

  .App > main {
    width:80vw;
    display:grid;
    place-items: center;
  }

  .socials {
    width:unset;
    grid-gap:2vw;
    gap:2vw;
  }
}

@keyframes rescale {
  from {
    transform: scale(0.5);
  }
  25% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(0.5);
  }
}
