.cardBox {
  float: left;
  margin: 1% 0 0 1%;
  perspective: 800px;
  transition: all 0.3s ease 0s;
  width: 100%;

}

.cardBox:hover .card {
  transform: rotateY( 180deg);
}

.card {
  background: #222;
  cursor: default;
  height: 300px;
  transform-style: preserve-3d;
  transition: transform 0.4s ease 0s;
  width: 100%;
  animation: giro 1s 1;
  border-radius: 5px;
}

.card p {
  margin-bottom: 1.8em;
}

.card .front,
.card .back {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  box-sizing: border-box;
  color: white;
  display: grid;
  height: 100%;
  position: absolute;
  text-align: center;
  width: 100%;
  place-items: center;
  border-radius: 5px;
}

.card .front {
  font-size: 1.3rem;
}

.card .back {
  transform: rotateY( 180deg);
  font-size: 1rem;

}

.card .back a:hover {
  background: #fff;
  color: #333;
  text-shadow: 0 0 1px #333;
}

.cardBox .card {
  animation: giro 1.5s 1;
}

@keyframes giro {
  from {
    transform: rotateY( 180deg);
  }
  to {
    transform: rotateY( 0deg);
  }
}

@media screen and (max-width: 767px) {
  .cardBox {
    /* margin-left: 2.8%;
    margin-top: 3%; */
    /* width: 46%; */
  }
  .card {
    height: 285px;
  }
  .cardBox:last-child {
    margin-bottom: 3%;
  }
}

@media screen and (max-width: 480px) {
  .cardBox {
    width: 94.5%;
  }
  .card {
    height: 260px;
  }
}
