.title-wrapper {
  display: grid;
  width:100%;
  height:50vw;
  place-items: center;
  font-size: 10vmin;
  background-size: cover;
  background-attachment: fixed;
  height: 40vh;
  color:white;
  margin-bottom: 2vh;
  background-color: hsla(0,0%,0%,0.5);
}

.img-text-wrapper {
  display: grid;
  place-items: center;
}

.img-text-wrapper > img {
  width:95%;
  /* border: 3px solid var(--dark-blue); */
  border-radius: 5px;
}

.content {
  width:100%;
}

h2 {
  margin-top:15vh;
  margin-bottom: 1vh;
}

p {
  padding:0vh 2vw;
  text-align: justify;
}

h2 + *, h3 + * {
  margin-top:0;
}
hr {
  margin-bottom: 0;
}
h3 {
  margin-bottom: 0.6rem;
  margin-top: 0.3rem;
  font-size: 1.1rem;
}

/* <-----------HOME-----------> */
.quote-slide {
  /* display:grid;
  place-items: center;
  height:100%;
  width:80%; */
  /* margin:0 auto; */
}
.track > .slide {
  margin:0 auto;
  width:100%;
  height:100%;
  display: grid;
  place-items: center;
}
.track > .slide > div {
  width: 80%;
  font-style: italic;
}

/* <-------ABOUT--------> */
#values-we-follow {
  display: grid;
  align-items: flex-start;
  justify-items:center;
  align-content: center;
  /* justify-content: center; */
  gap:2vh 2vw;
  padding-inline-start: 0;
  height:100%;
}

#values-we-follow li {
  list-style-type: none;
  display: grid;
  /* border: 3px solid var(--red); */
  /* border-radius: 5px; */
  max-width: 90vw;
  height: 100%;
  width:100%;
  /* overflow-y: auto; */
  padding:0 0.5rem;
  place-items: center;
}

